import React from 'react'

// COMPONENTS


import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
  HashRouter
} from 'react-router-dom'
import {get} from "lodash";

// COMPONENTS
import Header from "./components/header/Header";
import Homepage from './pages/homepage/Homepage';
import Event from './pages/event/Event';

class AppRouter extends React.Component {
  constructor(props) {
    super(props);
    this.pages = [
      {
        path: '/',
        exact: true,
        title: 'Homepage',
        component: Homepage
      },
      {
        path: '/search/:query',
        exact: false,
        title: 'Search',
        component: Homepage
      },
      {
        path: '/event/:id',
        exact: false,
        title: 'Event',
        component: Event
      }
    ]
  }

  componentWillMount() {

  }

  componentWillReceiveProps(nextProps) {

  }

  render() {
    return (
      <HashRouter>
        <div>
          <Header/>
          <Switch>
            {this.pages.map((page, index) => {
              return (
                <Route
                  key={index}
                  path={page.path}
                  exact={page.exact}
                  render={(match) => (
                    <page.component
                      match={match.match}
                      title={page.title}
                    />
                  )}/>
              )
            })
            }
            <Route component={Page404}/>
          </Switch>
        </div>
      </HashRouter>
    )
  }
}

const Page404 = () => {
  return (

    <div className='mainContainer'>
      <h2 className='text-center'>Not Found...</h2>
    </div>
  )
}


export default AppRouter





