import React from 'react'
import {connect} from 'react-redux';
import {get, filter} from 'lodash';
import {Link} from 'react-router-dom';
import SerpItem from "../../components/serpItem/SerpItem";


require('./style.scss')

class Homepage extends React.Component {
  constructor(props) {
    super(props)
    this.masonry = this.masonry.bind(this)
    this.state = {
      elementsInColumn: []
    }
  }

  componentWillMount() {
    const arrayList = Object.values(this.props.eventList.events)
    this.masonry(arrayList, 3)
  }

  masonry(arrayList, columns) {
    let myArrays = []
    for (let a = 0; a < columns; a++) {
      myArrays[a] = []
      for (let i = a; i < arrayList.length-1; i+=3){
        myArrays[a].push({...arrayList[i]})
      }
    }
    this.setState({
      elementsInColumn: myArrays
    }) 
  }

  render() {
    const query = get(this.props, 'match.params.query')
    return (
      <div className="serp">
        {
          this.state.elementsInColumn.map((eventColumn, col) => {
            return(
              <div
                key={`column_${col}`}
                className="serp__column">
                {
                  eventColumn.map((item, index) => {
                    return(
                      <div
                        key={index}
                        className="serp__column">
                        <Link
                          to={`/event/${item.id}`}>
                          <div
                            key={index}
                            className="serp__item">
                            <SerpItem
                              event={item}
                            />
                          </div>
                        </Link>
                      </div>
                    )
                  })
                }
              </div>
            )
          })
        }
      </div>
    )
  }
}

Homepage.propTypes = {}

function mapStateToProps(state) {
  return {
    eventList: state.rootReducer.eventList
  }
}

export default connect(mapStateToProps)(Homepage)


